import glanceSlideNavTabBar from '@/components/glance-SlideNavTabBar.vue';
let id = ""
var ctime = parseInt(Date.now());
const total = 10;
export default {
	components: {
		glanceSlideNavTabBar
	},
	data() {
		return {
			screenHeight: this.screenHeight,
			isShowRecommend: false,
			recommendList: [1, 2, 3, 4, 5, 6],
			tabIndex: 0,
			info: {},
			tabs: [{
					data: [],
					offset: 0,
					loadingType: 0
				},
				{
					data: [],
					offset: 0,
					loadingType: 0
				},
				{
					data: [],
					offset: 0,
					loadingType: 0
				},
				{
					data: [],
					offset: 0,
					loadingType: 0
				}
			],
			loadingText: {
				contentdown: '',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		};
	},
	computed: {
		recommendBoxWidth() {
			return uni.upx2px(this.recommendList.length * 270) + 'px';
		}
	},
	onLoad(options) {
		id = options.id
		this.init()
	},
	onPullDownRefresh() {
		this.init();
	},
	methods: {
		init() {
			this.api.classify.get_sub_category_header({
				categoryId: id
			}, res => {
				console.log(res)
				this.info = res.data
				uni.setNavigationBarTitle({
					title: res.data.categoryName
				});
			})
			this.getArticle()
		},
		getArticle() {
			this.api.classify.get_category_article({
				categoryId: id,
				type: this.tabIndex,
				ctime,
				offset: this.tabs[this.tabIndex].offset,
				total
			}, res => {
				console.log(res)
				this.tabs[this.tabIndex].data = res.data;
			})
		},
		toggleFollowed(el) {
			this.api.classify.toggle_followed({
				categoryId: id,
				action: el.isFollowed ? 0 : 1
			}, res => {
				console.log(res)
				this.info.isFollowed = !this.info.isFollowed
			})
		},
		showRecommend() {
			this.isShowRecommend = !this.isShowRecommend;
		},
		clickitem(index, val) {
			this.tabIndex = index
			this.changeTab(index);
		},
		changeSwiper(e) {
			this.tabIndex = e.target.current
			this.changeTab(e.target.current);
		},
		getMoreArticle() {
			console.log('111')
			this.tabs[this.tabIndex].offset += total;
			this.api.classify.get_category_article({
					categoryId: id,
					type: this.tabIndex,
					ctime,
					offset: this.tabs[this.tabIndex].offset,
					total
				}, res => {
					console.log(res)
					if (res.data.length) {
						this.tabs[this.tabIndex].data = this.tabs[this.tabIndex].data.concat(res.data);
						this.tabs[this.tabIndex].loadingType = 0;
					} else {
						this.tabs[this.tabIndex].loadingType = 2;
					}
				},
				err => {
					this.tabs[this.tabIndex].offset -= total;
					this.tabs[this.tabIndex].loadingType = 0;
				})
		},
		async changeTab(index) {
			if (!this.tabs[this.tabIndex].data.length) {
				await this.init();
			}
		},
		loadMore(i) {
			if (!this.tabs[this.tabIndex].data.length) {
				return;
			}
			if (this.tabs[i].loadingType !== 0) {
				return;
			} else {
				this.tabs[i].loadingType = 1;
				this.getMoreArticle();
			}
		},
		showOperate(e) {
			// console.log(e);
			// uni.getSystemInfo({
			// 	success: res => {
			// 		console.log(res.windowHeight);
			// 		if (e.detail.y + 220 > res.windowHeight) {
			// 			this.articleOffsetTop = e.detail.y - 210;
			// 		} else {
			// 			this.articleOffsetTop = e.detail.y + 20;
			// 		}
			// 		this.showArticleOperate = true;
			// 	}
			// });
		},
		hideArticleOperate() {
			this.showArticleOperate = false;
		},
	}
};
